较软，不能承受猫的体重，有可能会变形并翻倒，把猫砂撒得到处都是，更会把猫吓着。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t6.接下来的步骤就是把猫砂盆放到开着盖的马桶上。如果你的猫不太适应，你可能就需要把这个步骤一分为二：先把猫砂盆的一半架在马桶上，过一段时间再把整个猫砂盆都放到马桶上。把原来垫在猫砂盆下面的东西(报纸、电话号码簿等)全部挪走。稍待。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t7.接下来就到了最酷的部分啦。把猫砂盆拿走(哈哈!)，把金属盆架到马桶上。在盆里放上六厘米厚的猫砂。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t8.自然，在这个时候人要用马桶的时候都会先把金属盆拿开，方便之后再把金属盆放回原处。在接下来的一到两周时间里，你可能会感觉非常麻烦，但是只要想想从此之后你可以省一笔买猫砂的钱，也不用再给猫清理猫砂盆，这样做还是值得的!\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t9.注意观察你的猫，看它在使用金属盆的时候是把几只脚放在马桶上的;放在马桶圈上的脚越多，你就越走运，你的工作也会进行得越顺利，因为接下来你就要教它正确的“蹲坑”姿势了。在它方便的时候，教给它应该如何把脚放好，也就是说要把它的脚从盆里拿出来并放在马桶圈上(前脚在马桶圈中间的位置，后脚在后面)。如果它一开始就把三只脚放在盆里，或者——天哪，四只脚都在盆里，就先把两只前脚放在马桶圈上。每次它自己把两只前脚放在马桶圈上的时候都要表扬它一番。\n\t\t\t")]),_vm._v(" "),_c('h4',[_vm._v("三、猫咪憋着不用厕所，怎么办?")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t1.很多猫咪在开始训练之后，不象使用猫砂盆一样频繁地去上厕所。不过，至少应该每天大便一次，小便一到两次(最好是两次，不过有些猫咪喝水少，一次也不算憋得很严重)。小便猫咪往往不会一直憋着，而且也不太反感用马桶，大便如果憋了一天，第二天要想办法让他上厕所。可以使用的办法：\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t2.在吃完食后，把他关进厕所，看他会不会大便，关大概30分钟，大便后就放出来。如果关了30分钟，还不上厕所，就放出来。很多猫咪被关之后就上厕所了，可能是跟猫砂盆在一个房间，他总被提醒，有心理作用。有些猫咪会渐渐知道，如果不上厕所，就会被关;如果上了，就被解放，所以一旦被关，就主动去上厕所了。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t3.如果关了之后还是不肯上，你可以喂他吃他爱吃的猫罐头(或其它有水分，且他爱吃的食物)，吃完之后过15分钟再关起来。看他上不上厕所。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t4.如果还是不肯上，你需要喂他点毛球膏。毛球膏有软化大便的作用，有点象轻微的泻药。往往吃点毛球膏，就能够让猫咪最终憋不住去上厕所了。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t5.同样，憋尿的话，就多鼓励他喝水，或吃含很多水份的食物，他自然就憋不住去上厕所了。如果出现憋尿和大便的现象，说明你的进度太快，要停下来，给猫咪时间去适应。一般不要连续每天都扩大孔，除非你的猫咪根本不在意，若无其事地去上厕所，不在马桶前盘旋并哀怨地叫着。花花就是这样，我从没听她抱怨过一声，而且每天要NN好几次，BB也每天都有。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t6.另外请记得，孔扩大后，猫咪的不适应往往不是在当天出现，很多猫咪的反应是滞后的，大概要滞后3-4天，所以为了保险起见，扩孔的间隔最好在4天以上。\n\t\t\t")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t训练猫咪在马桶上排便可以减少猫咪吸食猫砂的粉尘，保证猫咪的基本健康，何乐而不为呢?在训练的过程中，主人只要有耐心，用正确的方法，相信猫咪很多就会学会用马桶排便了。\n\t\t\t")])])}]
